.about {
  position: relative;
  margin: 200px 0 0;

  @include vp-1023 {
    overflow: hidden;
    margin: 20px 0 0;
  }

  .container {
    position: relative;
    display: flex;
    justify-content: space-between;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 0 0 48.15%;

    @include vp-1023 {
      flex: 0 0 100%;
    }

    p {
      width: 90%;
      margin: 12px 0 0;

      @include vp-1279 {
        width: 92%;
      }

      @include vp-1023 {
        width: 101%;
      }
    }
  }

  &__caption {
    margin: 0;
    color: $color-transparent;

    background-image: $frame-blue-to-purple;
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__title {
    width: 95%;
    margin: 13px 0 0;

    @include vp-1023 {
      width: 100%;
      margin: 12px 0 0;
    }
  }

  &__link  {
    position: relative;
    z-index: 1;
    margin: 32px 0 0;
  }

  ul {
    list-style: none;
    display: flex;
    flex-direction: column;

    margin: 10px 0 0;
    padding: 0;
  }

  li {
    display: flex;
    align-items: center;
    margin: 14px 0 0 0;

    img {
      width: 16px;
      height: 16px;
    }

    span {
      display: flex;
      margin: 0 0 0 12px;

      font-weight: 400;
      font-size: 16px;
      line-height: 28px;
    }
  }

  &__img {
    position: relative;
    flex: 0 0 51.85%;
    min-height: 39vh;

    @include vp-1499 {
      min-height: auto;
    }

    @include vp-1023 {
      flex: 0 0 100%;
      margin: 0 -24px;

      &::before {
        content: "";
        display: block;
        padding-bottom: calc(1053 / 823 * 100%);
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    @include vp-767 {
      margin: 0 -16px;
    }

    &--desktop {
      @include vp-1023 {
        display: none;
      }
    }

    &--mobile {
      display: none;

      @include vp-1023 {
        display: block;
      }
    }

    .large-small-picture {
      height: 200%;
      width: 100%;
      margin: -10% 0 0 -10%;

      @include vp-1499 {
        // height: 134%;
        // width: 144.2%;
        // margin: -27.7% 0 0 -32.5%;
        // height: 220%;
        width: 169.5%;
        margin: -27.7% 0 0 -45.5%;
        // height: 199%;
        // width: 144.5%;
        // margin: -27.7% 0 0 -33%;
      }

      @include vp-1023 {
        position: relative;

        // height: 73.5vh;
        height: 135vh;
        // width: 169%;
        margin: 2.3% 0 0 -15.5%;
      }

      @include vp-767 {
        height: 130vh;
        width: 169%;
        margin: 2.3% 0 0 -35.5%;

        // height: 89.5vh;
        // margin: 2.3% 0 0 -38.5%;
      }

      @include vp-439 {
        width: 199%;
        height: 89.5vh;
        margin: 2.3% 0 0 -53.5%;
      }

      img {
        width: 100%;
        object-fit: cover;
      }
    }

    .large-small-picture__img--small {
      top: 0;
      left: 0;
    }

