    //   @include vp-439 {
    //     width: 199%;
    //     height: 89.5vh;
    //     margin: 2.3% 0 0 -53.5%;
    //   }

    //   img {
    //     width: 100%;
    //     object-fit: cover;
    //   }
    // }

    // .large-small-picture__img {
    //   // overflow: hidden;
    //   // position: absolute;
    //   // top: 0;
