    //   // width: 100%;
    //   // height: auto;

    //   // &::before {
    //   //   content: "";
    //   //   display: block;
    //   //   // padding-bottom: calc(823 / 1053 * 100%);
    //   //   padding-bottom: calc(1053 / 823 * 100%);
    //   // }

    //   img {
    //     position: absolute;
    //     top: 0;
    //     left: 0;
    //     width: 100%;
    //     height: 100%;
    //     object-fit: cover;
    //   }
    // }
  }

  &__decor {
    position: absolute;
    z-index: -1;
    bottom: -663px;
    left: -20px;

    @include vp-1023 {
      bottom: -414px;
      left: -9px;
    }

    svg {
      width: 1097px;
      height: 721px;
    }
  }

  &--about-us {
    overflow: hidden;
    margin: 0;
    padding: 191px 0 201px;

    @include vp-1023 {
      padding: 102px 0 340px;
    }

    .about__content {
      flex: 0 0 52%;
    }

    .about__title {
      width: 100%;
      margin: 13px 0 0;

      @include vp-1023 {
        width: 100%;
        margin: 10px 0 0;
      }
    }

    ul {
      margin: 17px 0 0;

      @include vp-1023 {
        width: 101%;
        margin: 19px 0 0;
      }
    }

    li {
      margin: 0;

      p {
        width: 100%;
        margin: 0;
      }
    }

    .about__img {
      flex: 0 0 48%;
      order: 1;

      @include vp-1023 {
        flex: 0 0 100%;

        &::before {
          padding-bottom: 98%;
        }
      }

      .large-small-picture {
        height: 200%;
        width: 100%;
        margin: -10% 0 0 10%;

        @include vp-1499 {
        // height: 145%;
          width: 192%;
          margin: -26% 0 0 -28.5%;

          height: 161%;
          // width: 164%;
          // margin: -26.5% 0 0 -14.5%;
        }

        @include vp-1023 {
          position: relative;

          height: 120vh;
          width: 187%;
          margin: -7% 0 0 -10%;
        }

        @include vp-767 {
          // height: 84vh;
          width: 220%;
          // margin: -11.7% 0 0 -26%;
          margin: -11.7% 0 0 -21%;
        }

        @include vp-439 {
          height: 84vh;
        }

        img {
          width: 100%;
          object-fit: cover;
        }
      }

      .large-small-picture__img--small {
        // top: 6px;
        // left: -39px;
        top: 0;
        left: 0;

        // @include vp-1023 {
        //   top: 3px;
        //   left: -32px;
        // }

        img {
          width: 100%;
          // max-width: 108%;
        }
      }
    }

    .about__decor {
      position: absolute;
      z-index: -1;
      display: none;

      @include vp-1023 {
        display: block;

        top: 742px;
        left: -9px;
      }

      svg {
        width: 1097px;
        height: 721px;
      }
    }
  }

  &.anim-image {
    transition: transform $trans-600, opacity $trans-default;
    transform: translateY(30%);
    opacity: 0;

    will-change: transform, opacity;

    &.is-active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
