.intro-about {
  position: relative;
  // overflow: hidden;
  padding: 70px 0 0;

  @include vp-1023 {
    padding: 38px 0 0;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    margin: 0 auto;
    padding: 0 0 74px;

    &::after {
      content: "";
      position: absolute;

      bottom: 0;
      left: 0;
      height: 1px;
      width: 100%;

      background-image: $frame-blue-to-purple;
    }

    @include vp-1023 {
      padding: 0 0 64px;
    }
  }

  &__title {
    margin: 0 auto;
    text-align: center;

    @include vp-1023 {
      font-size: 40px;
      line-height: 58px;
      letter-spacing: -0.2px;
    }
  }

  &__breadcrumps {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    margin: 30px 0 0;

    @include vp-1023 {
      margin: 24px 0 0;
    }
  }

  &__link  {
    position: relative;
    z-index: 1;
    margin: 0 8px 0 0;

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      top: 50%;

      height: 200%;
      width: 130%;
    }
  }

  &__icon  {
    margin: -3px 7px 0 0;

    svg {
      width: 10px;
      height: 6px;
      transform: rotate(90deg);
    }
  }

  &__decor {
    position: absolute;
    z-index: -1;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 669px;
    height: 367px;

    @include vp-1023 {
      top: 30%;
      width: 430px;
      height: 220px;
    }
  }
}
