.footer {
  position: relative;
  z-index: 9;

  padding: 40px 0;
  line-height: 28px;

  @include vp-1023 {
    overflow: hidden;
    padding: 14px 0;
  }

  &__caption {
    margin: 2px 0 0;
  }

  &__wrapper {
    position: relative;
    z-index: 1;
    display: flex;
    flex-wrap: wrap;

    padding: 2px 0 0;

    @include vp-1023 {
      flex-direction: row;
      flex-wrap: wrap;
    }
  }

  &__logo {
    width: 101px;
    height: 30px;

    @include vp-1023 {
      margin: 8px 20px 0 0;
    }
  }

  &__socials {
    @include vp-1023 {
      margin: 0 0 0 auto;
    }

    .socials__list {
      display: flex;
      flex-wrap: wrap;
      list-style: none;

      margin: 120px 0 0 -20px;
      padding: 0 50px 0 0;

      @include vp-1023 {
        margin: -20px 0 0;
        padding: 0;
      }

      @include vp-419 {
        margin: 20px 0 0;
      }
    }

    .socials__item {
      display: inline-flex;
      justify-content: center;
      align-items: center;

      width: 46px;
      height: 46px;
      margin: 20px 0 0 20px;

      border: 1px double transparent;
      border-radius: 50%;
      color: $color-default-white;
      background-image: $black-to-black, $frame-blue-to-purple;
      background-origin: border-box;
      background-clip: padding-box, border-box;
    }

    .socials__link {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0 5px 0 0;
  }

  &__item {
    margin: 16px 0 0;
  }

  &__link {
    position: relative;
    display: flex;
    flex-wrap: wrap;

    color: $color-default-white;

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      top: 50%;

      height: 150%;
      width: 100%;
    }
  }

  &__block {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    flex: 0 0 35.1%;

    margin: -2px 0 0;

    @include vp-1023 {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
      flex: 0 0 100%;
    }
  }

  &__quicklinks {
    flex: 0 0 19.5%;

    @include vp-1023 {
      flex: 0 0 47%;
      margin: 55px 0 0 0;
    }
  }

  &__support {
    flex: 0 0 18.2%;

    @include vp-1023 {
      flex: 0 0 50%;
      margin: 55px 0 0 0;
    }
  }

  &__help {
    flex: 0 0 27.2%;

    @include vp-1023 {
      flex: 0 0 100%;
      margin: 60px 0 0 0;
    }

    svg {
      margin: 5px 0 0;

      color: $color-darkgrey;
    }

    .footer__link {
      user-select: auto;
    }
  }

  &__help-icon {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    width: 20px;
    margin: 0 8px 0 0;
  }

  &__copyright {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    text-align: center;

    margin: 33px 0 0;
    padding: 32px 0 0;

    color: $color-default-white;

    &::after {
    content: "";
    position: absolute;

    top: 0;
    left: 0;
    height: 1px;
    width: 100%;

    background-image: $frame-blue-to-purple;
  }

    @include vp-1023 {
      margin: 47px 0 0;
    }
  }

  &__decor {
    position: absolute;

    bottom: 0;
    left: 0;

    @include vp-1023 {
      bottom: -7px;
      width: 637px;
      height: 314px;
    }

    img {
      width: 1010px;
      height: 498px;

      @include vp-1023 {
        width: 637px;
        height: 314px;
      }
    }
  }

  &.anim-image {
    transition: transform $trans-600, opacity $trans-default;
    transform: translateY(30%);
    opacity: 0;

    will-change: transform, opacity;

    &.is-active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
