.main-nav {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  z-index: 1;

  transition: height $trans-menu;
  transition: height 1s;

  @include vp-1023 {
    justify-content: center;
  }

  &.is-closed {
    @include vp-1023 {
      height: 0;
    }
  }

  &.is-opened {
    @include vp-1023 {
      z-index: 50;
      height: 100vh;

      opacity: 1;

      transition: height $trans-menu;
    }
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;

    @include vp-1023 {
      flex-direction: column;
      margin: 100px 0 0;
      opacity: 0;

      // transition: opacity $trans-menu;
      transition: opacity $trans-menu, visibility $trans-menu;
    }
  }

  &.is-closed .main-nav__wrapper {
    @include vp-1023 {
      // pointer-events: none;
      visibility: hidden;
    }
  }

  &.is-opened .main-nav__wrapper {
    @include vp-1023 {
      // pointer-events: auto;
      visibility: visible;
      opacity: 1;

      transition: opacity $trans-menu 0.5s, visibility $trans-menu;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;

    margin: 0 131px 0 60px;
    padding: 0;

    list-style: none;
    font-weight: 300;
    line-height: 28px;

    @include vp-1439 {
      margin: 0 40px 0 20px;
    }

    @include vp-1023 {
      flex-direction: column;
      align-items: center;
      margin: 0;
    }
  }

  &__item {
    margin: 2px 65px 0 0;

    @include vp-1023 {
      margin: 20px 0 0 0;
    }
  }

  &__link {
    position: relative;
    color: $color-default-white;

    &.is-active {
      font-weight: 500;
    }

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      top: 50%;

      height: 200%;
      width: 130%;
    }
  }

  &__toggle.btn {
    z-index: 55;
    display: none;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    width: 32px;
    height: 32px;
    padding: 0;

    cursor: pointer;

    transition: opacity $trans-default;

    @include vp-1023 {
      display: flex;
    }
  }

  &.is-closed &__toggle {
    position: absolute;
    top: -32px;
    right: 0;

    &::after {
      content: "";
      position: absolute;

      height: 8px;
      width: 35%;

      background-image: url("../img/sprite/icon-burger.svg");
      background-position: center;
      background-repeat: no-repeat;

      transition: background $trans-menu;
    }
  }

  &.is-opened &__toggle {
    position: absolute;
    top: -32px;
    right: 0;

    &::after {
      content: "";
      position: absolute;
      height: 10px;
      width: 10px;

      background-image: url("../img/sprite/icon-close.svg");
      background-position: center;
      background-repeat: no-repeat;

      transition: background $trans-menu;
    }

    &:focus {
      &::after {
        opacity: 0.5;
      }
    }
  }

  &__scroll {
    position: relative;
    z-index: 1;
    margin: -10px 0 0 auto;

    @include vp-1023 {
      margin: 40px auto 0;
    }

    &.btn--frame {
      padding: 12px 24px;
    }
  }
}
