.service {
  overflow: hidden;
  padding: 160px 0 150px;

  @include vp-1023 {
    padding: 103px 0 32px;
  }

  .container {
    position: relative;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;

    @include vp-1023 {
      flex-direction: column;
    }

    p {
      flex: 0 0 55.5%;
      margin: 7px 0 0 auto;
      padding: 0 20px 0 0;

      @include vp-1023 {
        flex: 0 0 100%;
        margin: 12px 0 0;
        padding: 0;
      }
    }
  }

  &__block {
    flex: 0 0 44.4%;
    padding: 0 20px 0 0;

    @include vp-1023 {
      flex: 0 0 100%;
      padding: 0;
    }

    .service__title {
      margin: 10px 0 0;

      @include vp-1023 {
        margin: 8px 0 0;
      }
    }
  }

  &__caption {
    margin: 6px 0 0;
    color: $color-transparent;

    background-image: $frame-blue-to-purple;
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;

    margin: 16px 0 0 -2.44%;
    padding: 0;

    @include vp-1023 {
      flex-direction: column;
      margin: -7px 0 0;
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 0 0 30.89%;

    min-height: 291px;
    top: 0;
    margin: 30px 0 0 2.44%;
    padding: 38px 32px 25px;

    // border: 1px double transparent;
    background-image: $semiblack-to-semiblack;
    // background-origin: border-box;
    // background-clip: padding-box, border-box;

    // transition: background $trans-default;

    &:nth-child(3n+1) {
      top: 0;
    }

    &:nth-child(3n+2) {
      top: 60px;

      @include vp-1023 {
        top: 0;
      }
    }

    &:nth-child(3n) {
      top: 120px;

      @include vp-1023 {
        top: 0;
      }
    }

    @include vp-1023 {
      flex: 0 0 100%;
      margin: 31px 0 0;
    }

    &::before {
      content: '';
      position: absolute;
      top: 48%;
      left: 1px;
      right: 1px;
      bottom: 1px;
      z-index: 3;
      background: $black-to-transparent;
      opacity: 0;

      transition: opacity $trans-default;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      border: 1px double transparent;
      background-image: $semiblack-to-semiblack, $blue-to-purple;
      background-origin: border-box;
      background-clip: padding-box, border-box;
      opacity: 0;

      transition: opacity $trans-600;
    }

    .service__border {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      border: 1px double transparent;
      background-image: $semiblack-to-semiblack, $white-to-transparent;
      background-origin: border-box;
      background-clip: padding-box, border-box;
      opacity: 1;

      transition: opacity $trans-default;
    }

    @include hover {
      .service__link {
        opacity: 1;

        @include hover {
          &::before {
            opacity: 1;
          }
        }

        @include hover-focus {
          &::before {
            opacity: 1;
          }
        }
      }

      .service__border {
        opacity: 0;
      }

      &::before, &::after {
        opacity: 1;
      }
    }

    img {
      z-index: 2;
      width: 48px;
      height: 48px;
    }

    span {
      z-index: 2;
      width: 97%;
      margin: 10px 0 0;

      display: -webkit-box;
      -webkit-line-clamp: 3; // количество строк
      overflow: hidden;
      -webkit-box-orient: vertical;
      text-overflow: ellipsis;

      @include vp-1023 {
        width: 94%;
      }
    }

    .service__link {
      display: flex;
      position: absolute;
      z-index: 3;
      left: 50%;
      bottom: 31px;
      transform: translateX(-50%);
      justify-content: center;
      align-items: center;

      min-width: 177px;
      padding: 17px 33px;

      opacity: 0;
      transition: opacity $trans-default 0.3s;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 3;
        background: $semiwhite-to-semiwhite;
        opacity: 0;

        transition: opacity $trans-default;
      }

      span {
        z-index: 1;
        margin: 0;

        font-size: 14px;
        font-weight: 700;
        line-height: 14px; /* 100% */
        letter-spacing: 1.5px;
        text-transform: uppercase;
      }
    }
  }

  &__card-title {
    margin: 60px 0 0;
    z-index: 2;
  }

  &__decor {
    position: absolute;
    z-index: -1;

    top: -510px;
    right: -710px;

    @include vp-1023 {
      display: none;
    }

    img {
      width: 1739px;
      height: 1134px;
    }
  }

  &.anim-image {
    transition: transform $trans-600, opacity $trans-default;
    transform: translateY(30%);
    opacity: 0;

    will-change: transform, opacity;

    &.is-active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
