.container {
  position: relative;
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 120px;

  @include vp-1439 {
    padding: 0 40px;
  }

  @include vp-1023 {
    padding: 0 24px;
  }

  @include vp-767 {
    padding: 0 16px;
  }

  &--sticky {
    display: flex;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  &--no-padding {
    padding: 0;
  }
}
