.pricing {
  position: relative;
  overflow: hidden;
  padding: 148px 0 190px;

  @include vp-1499 {
    padding: 148px 0 150px;
  }

  @include vp-1023 {
    padding: 100px 0 90px;
  }

  .container {
    position: relative;
  }

  &__content {
    p {
      width: 50%;
      margin: 16px 0 0;

      @include vp-1023 {
        width: 100%;
      }
    }

    p.pricing__title {
      width: max-content;
      margin: 12px 0 0;

      @include vp-1023 {
        width: 100%;
      }
    }
  }

  &__caption {
    margin: 3px 0 0;
    color: $color-transparent;

    background-image: $frame-blue-to-purple;
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__link  {
    margin: 14px 0 0;
  }

  &__wrapper {
    position: relative;
    margin: 40px 0 0 0;
    padding: 49px 42px 55px;

    border: 1px double transparent;
    background-image: $darkgrey-to-darkgrey, $white-to-transparent;
    background-origin: border-box;
    background-clip: padding-box, border-box;

    @include vp-1023 {
      margin: 48px 0 0 0;
      padding: 26px 22px 405px;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    flex-direction: column;

    width: 68%;
    margin: -28px 0 0;
    padding: 0 0 0 56px;

    border-right: 1px solid rgba($color-default-white, 0.1);

    @include vp-1023 {
      width: 100%;
      margin: -27px 0 0 0;
      padding: 0 0 48px 56px;

      border-bottom: 1px solid rgba($color-default-white, 0.1);
      border-right: none;
    }
  }

  &__item {
    position: relative;

    width: 83%;
    margin: 32px 0 0 0;

    @include vp-1023 {
      width: 100%;
    }

    img {
      position: absolute;
      top: 0;
      left: -58px;
      width: 42px;
      height: 42px;
    }

    p {
      margin: 2px 0 0;
    }

    span {
      display: flex;
      margin: 10px 0 0 0;
    }
  }

  &__img {
    position: absolute;
    bottom: 0;
    right: 120px;

    img {
      width: 100%;
      object-fit: cover;
    }

    @include vp-1439 {
      right: 7%;
    }

    @include vp-1023 {
      right: -11.5%;

      img {
        width: 80%;
      }
    }

    @include vp-767 {
      right: -20.5%;

      img {
        width: 71%;
      }
    }
  }

  &__block {
    position: absolute;
    z-index: 1;
    bottom: -48px;
    right: 291px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 27%;

    padding: 20px 69px 18px;

    border: 1px double transparent;
    background-image: $semiblack-to-semiblack, $frame-blue-to-purple;
    background-origin: border-box;
    background-clip: padding-box, border-box;

    @include vp-1439 {
      right: 13%;
      max-width: 30%;
    }

    @include vp-1023 {
      bottom: -88px;
      right: 18.3%;

      max-width: 50%;
    }

    @include vp-767 {
      max-width: 75%;
    }
  }

  &__teaser {
    margin: 0;
    text-align: center;
  }

  &__price {
    margin: 0;

    text-align: center;

    font-weight: 600;
    font-size: 60px;
    line-height: 66px;
  }

  &__note {
    margin: 12px 0 0;
    text-align: center;
  }

  &__decor {
    position: absolute;
    z-index: -1;

    top: -4px;
    right: -25.5%;

    @include vp-1023 {
      bottom: -131px;
      top: unset;
      right: -79%;
      width: 255%;
      height: auto;
    }

    img {
      width: 100%;
      object-fit: cover;
    }
  }

  &.anim-image {
    transition: transform $trans-600, opacity $trans-default;
    transform: translateY(30%);
    opacity: 0;

    will-change: transform, opacity;

    &.is-active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
