.intro {
  position: relative;
  overflow: hidden;
  padding: 77px 0 0;

  @include vp-1023 {
    padding: 38px 0 0;
  }

  .container {
    display: flex;
    flex-direction: column;
  }

  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 0 0 45%;

    p {
      width: 80%;
      margin: 12px 0 0;

      @include vp-1279 {
        width: 92%;
      }

      @include vp-1023 {
        width: 92%;
      }
    }

    .intro__picture {
      position: absolute;
      z-index: 1;
      bottom: 67px;
      right: -10px;

      @include vp-1023 {
        bottom: -420px;
        left: 0;
      }

      @include vp-767 {
        bottom: -252px;
      }

      img {
        width: 196px;
        height: 128px;
        object-fit: cover;
      }
    }
  }

  &__title {
    margin: 0;
  }

  &__link  {
    position: relative;
    z-index: 2;
    margin: 32px 0 0;
  }

  &__block {
    display: flex;
    flex-wrap: wrap;

    margin: 82px 0 0;
    padding: 0 0 20px;

    @include vp-1023 {
      margin: 32px 0 0;
    }
  }

  &__list {
    list-style: none;
    display: flex;

    margin: 0 0 0 15px;
    padding: 0;

    @include vp-1023 {
      margin: 0 0 0 13px;
    }
  }

  &__item {
    border-radius: 50%;
    margin: 0 0 0 -13px;
    border: 1px solid $color-default-black;

    img {
      width: 102%;
      max-width: 102%;
      margin: -1px 0 0 -1px;
      object-fit: cover;
    }
  }

  &__teaser {
    position: relative;
    display: flex;
    align-items: center;

    margin: 0 0 0 18px;

    b {
      color: $color-default-white;
      font-weight: 600;
    }

    img {
      position: absolute;
      bottom: 14px;
      left: -6px;
      width: 48px;
      height: 7px;
      object-fit: cover;
    }
  }

  &__img {
    position: relative;
    flex: 0 0 44.9%;

    @include vp-1023 {
      left: 33.6%;
      flex: 0 0 100%;
      margin: -26px 0 0 44px;
    }

    .intro__big-picture {
      margin: -73px 0 0 13px;

      @include vp-1023 {
        margin: 0 0 0 0;
      }
    }

    .intro__light {
      position: absolute;
      z-index: -1;
      bottom: -150px;
      left: -274px;

      @include vp-1023 {
        bottom: -258px;
        left: -255px;
      }

      img {
        width: 1073px;
        height: auto;
        max-width: 200%;
      }
    }

    img {
      width: 100%;
      max-width: 100%;

      object-fit: cover;

      // @include vp-1499 {
      //   width: 100%;
      //   max-width: 100%;
      // }

      // @include vp-1279 {
      //   margin: -100px 0 0 -33px;
      // }

      // @include vp-1023 {
      //   width: 115%;
      //   max-width: 115%;
      //   margin: -92px 0 0 50px;
      // }

      // @include vp-767 {
      //   width: 144.1%;
      //   max-width: 144.1%;
      //   margin: -92px 0 0 64px;
      // }
    }
  }

  &__decor {
    position: absolute;
    z-index: -1;
    bottom: 96px;
    left: 0;

    @include vp-1023 {
      bottom: unset;
      top: 168px;
    }

    svg {
      width: 1097px;
      height: 721px;
    }
  }
}
