.start {
  position: relative;
  padding: 80px 0 0;

  @include vp-1023 {
    padding: 50px 0 0;
  }

  .container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;

    width: 100%;
  }

  &__caption {
    margin: 0 auto;

    text-align: center;
    color: $color-transparent;

    background-image: $frame-blue-to-purple;
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__title {
    width: 55%;
    margin: 12px auto 0;

    text-align: center;

    @include vp-1023 {
      width: 100%;
    }
  }

  &__link  {
    position: relative;
    display: flex;
    z-index: 1;
    margin: 46px 0 0;

    @include vp-1023 {
      margin: 32px 0 0;
    }
  }

  &__video {
    margin: 48px 0 0;
    width: 100%;

    @include vp-1023 {
      margin: 33px 0 0;
    }
  }

  &__decor {
    position: absolute;
    z-index: -1;

    top: -395px;
    left: -43%;

    @include vp-1023 {
      display: none;
    }

    img {
      width: 1739px;
      height: 1134px;
    }
  }

  &.anim-image {
    transition: transform $trans-600, opacity $trans-default;
    transform: translateY(30%);
    opacity: 0;

    will-change: transform, opacity;

    &.is-active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
