.team {
  margin: 66px 0 0;
  padding: 0 0 105px;

  @include vp-1023 {
    margin: 52px 0 0;
    padding: 0 0 2px;
  }

  .container {
    position: relative;

    @include vp-1023 {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__caption {
    max-width: 80%;
    margin: 5px 0 0;
    color: $color-transparent;

    background-image: $frame-blue-to-purple;
    -webkit-background-clip: text;
    background-clip: text;

    @include vp-1023 {
      margin: 0 auto;
      text-align: center;
    }
  }

  p.team__title {
    max-width: 80%;
    margin: 12px 0 0;

    @include vp-1023 {
      max-width: none;
      margin: 11px auto 0;

      text-align: center;
    }
  }

  &__link {
    position: absolute;
    top: 40px;
    right: 120px;
    display: flex;
    justify-content: center;
    align-items: center;

    margin: 0 0 4px auto;
    padding: 17px 37px;

    background: $semiwhite-to-semiwhite;

    @include vp-1023 {
      position: static;
      margin: 32px 0 0;
    }

    span {
      margin: 0;

      font-size: 14px;
      font-weight: 700;
      line-height: 14px; /* 100% */
      letter-spacing: 1.5px;
      text-transform: uppercase;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    margin: 32px 0 0 -0.744%;
    padding: 0;

    @include vp-1023 {
      margin: 8px auto 0;
      width: 50%;
    }

    @include vp-767 {
      width: 74%;
    }
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 24.25%;

    min-height: 45vh;
    margin: 9px 0 0 0.744%;

    background-image: $semiblack-to-semiblack;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      border: 1px double transparent;
      background-image: $semiblack-to-semiblack, $blue-to-purple;
      background-origin: border-box;
      background-clip: padding-box, border-box;
      opacity: 0;

      transition: opacity $trans-default;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      border: 1px double transparent;
      background-image: $semiblack-to-semiblack, $white-to-transparent;
      background-origin: border-box;
      background-clip: padding-box, border-box;
      opacity: 1;

      transition: opacity $trans-default;
    }

    @include vp-1499 {
      min-height: 57.5vh;
    }

    @include vp-1023 {
      flex: 0 0 100%;
      margin: 24px 0 0;
    }

    @include vp-767 {
      min-height: 57.7vh;
    }

    @include hover {
      // background-image: $semiblack-to-semiblack, $blue-to-purple;
      &::before {
        opacity: 0;
      }

      &::after {
        opacity: 1;
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__block {
    position: absolute;
    z-index: 5;
    left: 50%;
    bottom: 17px;
    transform: translateX(-50%);

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__name {
    margin: 0;
    text-align: center;

    @include vp-419 {
      font-size: 12px;
    }
  }

  &__position {
    margin: 0;
    text-align: center;

    @include vp-419 {
      font-size: 12px;
    }
  }

  &__img {
    position: relative;
    z-index: 2;
    width: calc(100% - 18px);
    height: calc(100% - 18px);

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      background: $blue-to-transparent;
    }
  }

  &.anim-image {
    transition: transform $trans-600, opacity $trans-default;
    transform: translateY(30%);
    opacity: 0;

    will-change: transform, opacity;

    &.is-active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
