.choose {
  position: relative;
  overflow: hidden;
  margin: 103px 0 0;
  padding: 126px 0 130px;

  @include vp-1499 {
    padding: 126px 0 30px;
  }

  @include vp-1023 {
    margin: 23px 0 0;
    padding: 26px 0 0;
  }

  .container {
    position: relative;
    display: flex;
    justify-content: space-between;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 0 0 48.15%;

    margin: 0 0 79px;

    @include vp-1023 {
      flex: 0 0 100%;
      margin: -11px 0 0;
    }
  }

  .accordion__wrapper p {
    width: 100%;
    margin: 11px 0 3px;

    font-weight: 400;

    @include vp-1279 {
      width: 92%;
    }

    @include vp-1023 {
      width: 100%;
    }
  }

  &__caption {
    margin: 0;
    color: $color-transparent;

    background-image: $frame-blue-to-purple;
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__title {
    width: 95%;
    margin: 12px 0 0;

    @include vp-1023 {
      width: 100%;
    }
  }

  &__subtitle {
    margin: 0 0 0;
  }

  &__img {
    position: relative;
    flex: 0 0 51.85%;
    min-height: 67vh;

    @include vp-1499 {
      min-height: auto;
    }

    @include vp-1023 {
      flex: 0 0 100%;
      margin: 0 -24px;

      &::before {
        content: "";
        display: block;
        padding-bottom: calc(1028 / 747 * 100%);
      }

      img {
        position: absolute;
        top: -80px;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;

        @include vp-439 {
          top: -40px;
        }
      }
    }

    @include vp-767 {
      margin: 0 -16px;
    }

    &--desktop {
      @include vp-1023 {
        display: none;
      }
    }

    &--mobile {
      display: none;

      @include vp-1023 {
        display: block;
      }
    }

    .large-small-picture {
      height: 200%;
      width: 100%;
      margin: -10% 0 0 10%;

      @include vp-1499 {
      // height: 145%;
      // width: 141.2%;
      // margin: -23% 0 0 0.3%;
        // height: 157%;
        width: 168.5%;
        margin: -41% 0 0 -12.5%;

        // height: 160%;
        // width: 143.5%;
        // margin: -41% 0 0 0;
      }

      @include vp-1023 {
        position: relative;

        height: 170vh;
        width: 166.6%;
        margin: -18.2% 0 0 3.2%;
      }

      @include vp-767 {
        height: 130vh;
        width: 141.6%;
        margin: -24% 0 0 0;
      }

      @include vp-439 {
        height: 103.7vh;
        width: 196.6%;
        margin: -24% 0 0 -11.8%;
      }

      img {
        width: 100%;
        object-fit: cover;
      }
    }

    .large-small-picture__img--small {
      // top: 103px;
      // left: 23px;
      // top: 115px;
      // left: -27px;
      top: 99px;
      left: -11px;

      @include vp-1023 {
        top: 72px;
        left: -5px;
        // top: 109px;
        // left: 4px;
      }

      img {
        // width: 105%;
        // max-width: 105%;
        width: 100%;
        max-width: 100%;

        // @include vp-767 {
        //   width: 100%;
        //   max-width: 100%;
        // }
      }
    }
  }

  .accordion {
    position: relative;
    z-index: 1;
    width: 95%;

    @include vp-1023 {
      width: 100%;
    }
  }

  &--about {
    margin: 91px 0 0;
    padding: 126px 0 0;

    @include vp-1023 {
      margin: 0;
      padding: 21px 0 0;
    }

    .choose__img {
      flex: 0 0 50.85%;

      @include vp-1023 {
        flex: 0 0 100%;
      }
    }

    .large-small-picture {
      height: 250%;
      width: 100%;
      margin: -10% 0 0 -10%;

      @include vp-1499 {
        height: 160%;
        // width: 156.1%;
        // margin: -25.7% 0 0 -31.3%;

        width: 184.1%;
        margin: -25.7% 0 0 -45.3%;
      }

      @include vp-1023 {
        position: relative;
        // left: 50%;
        // transform: translateX(-50%);

        height: 130vh;
        width: 195.6%;
        // max-width: 195.6%;
        margin: 0 0 0 -8.5%;
        // margin: 0;

      }

      @include vp-767 {
        // height: 96vh;
        width: 231%;
        margin: 0 0 0 -18.5%;

        // margin: 0 0 0 -13.5%;
      }

      @include vp-439 {
        height: 96vh;
        margin: 0 0 0 -30.5%;
      }

      img {
        width: 100%;
        object-fit: cover;
      }
    }

    .large-small-picture__img--small {
      // top: 7px;
      // left: -32px;
      top: 0;
      left: 0;

      // @include vp-1023 {
      //   left: -26px;
      // }

      img {
        // width: 107.5%;
        // max-width: 107.5%;
        width: 100%;
        max-width: 100%;
      }
    }

    .choose__content {
      flex: 0 0 49.15%;
    }

    .accordion {
      width: 93%;

      @include vp-1023 {
        width: 100%;
      }
    }
  }

  &.anim-image {
    transition: transform $trans-600, opacity $trans-default;
    transform: translateY(30%);
    opacity: 0;

    will-change: transform, opacity;

    &.is-active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
