.custom-input,
.custom-textarea {
  position: relative;

  display: flex;
  width: 530px;
  padding: 14px 0 28px 0;

  @media screen and (max-width: 767px) {
    padding: 4px 0 48px 0;
    width: 100%;
  }

  label {
    display: flex;
    align-items: center;
    width: 100%;

    @media screen and (max-width: 767px) {
      align-items: flex-start;
      flex-direction: column;
    }

    input,
    textarea {
      flex-grow: 1;
      max-width: 300px;
      min-height: 48px;
      margin-left: auto;
      padding: 10px;

      border-radius: 5px;
      border: none;

      @media screen and (max-width: 767px) {
        max-width: unset;
        width: 100%;
      }
    }
  }

  &__label {
    @media screen and (max-width: 767px) {
      margin: 0 0 18px 0;
    }
  }

  &__error {
    position: absolute;
    right: 0;
    bottom: 35px;

    color: $color-error;

    opacity: 0;

    transition: opacity 0.3s ease;

    @media screen and (max-width: 767px) {
      bottom: 20px;
    }
  }

  &.is-invalid {
    input,
    textarea {
      outline: 1px solid $color-error;

      &:focus,
      &:hover {
        border: 1px solid $color-error;
      }
    }
  }

  &.is-valid {
    input {
      outline: 1px solid $color-success;

      &:focus,
      &:hover {
        outline: 1px solid $color-success;
      }
    }
  }

  .input-message {
    position: absolute;
    left: 0;
    bottom: 5px;

    &.is-invalid {
      color: $color-error;
    }

    &.is-valid {
      outline: 1px solid $color-success;
    }
  }
}
