.header {
  position: relative;
  padding: 40px 0;

  @include vp-1023 {
    padding: 43px 0;
  }

  .container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include vp-1023 {
      flex-direction: column;
      width: 100vw;
    }
  }

  &__logo {
    flex-shrink: 0;
    align-self: flex-start;
    margin: 0 80px 0 0;

    @include vp-1439 {
      margin: 0 60px 0 0;
    }

    @include vp-1279 {
      margin: 0 40px 0 0;
    }

    img {
      width: 101px;
      height: 30px;
    }
  }
}

.header--fixed {
  // position: fixed;
  position: sticky;
  top: 0;
  z-index: 10;

  width: 100%;
  max-width: 100vw;

  background-color: rgba($color-default-black, 0.5);
  backdrop-filter: blur(5px);

  transition: background-color $trans-default;
}

.header--bg {
  background-color: rgba($color-default-black, 1);
}
