.features {
  position: relative;
  margin: 6px 0 0;

  .container {
    position: relative;
  }

  &__wrapper {
    margin: -6px 0 0 0;
    padding: 42px 5.33% 53px;

    border: 1px double transparent;
    background-image: $semiblack-to-semiblack, $white-to-transparent;
    background-origin: border-box;
    background-clip: padding-box, border-box;

    @include vp-1023 {
      padding: 36px;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;

    margin: -50px 0 0 -4.45%;
    padding: 0;

    @include vp-1023 {
      flex-direction: column;
      margin: -50px 0 0;
    }
  }

  &__item {
    flex: 0 0 28.8%;
    margin: 50px 0 0 4.45%;

    @include vp-1023 {
      flex: 0 0 100%;
      margin: 52px 0 0;
    }

    svg {
      width: 70px;
      height: 70px;
    }

    span {
      display: flex;
      margin: 10px 0 0;
    }
  }

  &__subtitle {
    display: flex;
    margin: 42px 0 0;
  }

  &--about {
    margin: 40px 0 0;

    @include vp-1023 {
      margin: -241px 0 0;
    }

    .container {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    .features__caption {
      max-width: max-content;
      margin: 0 auto;

      text-align: center;
      color: $color-transparent;

      background-image: $frame-blue-to-purple;
      -webkit-background-clip: text;
      background-clip: text;
    }

    .features__title {
      justify-content: center;
      width: 55%;
      margin: 12px auto 0;

      text-align: center;

      @include vp-1023 {
        width: 100%;
      }
    }

    .features__wrapper {
      margin: 54px 0 0;

      @include vp-1023 {
        margin: 33px 0 0;
      }
    }
  }

  &__decor {
    position: absolute;
    z-index: -1;
    bottom: 137px;
    left: -20px;

    @include vp-1023 {
      display: none;
    }

    svg {
      width: 1097px;
      height: 721px;
    }
  }

  &.anim-image {
    transition: transform $trans-600, opacity $trans-default;
    transform: translateY(30%);
    opacity: 0;

    will-change: transform, opacity;

    &.is-active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
