.partners {
  margin: 48px 0 0;

  @include vp-1023 {
    margin: 98px 0 0;
  }

  .container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__content {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__caption {
    max-width: max-content;
    margin: 0 auto;
    text-align: center;

    color: $color-transparent;

    background-image: $frame-blue-to-purple;
    -webkit-background-clip: text;
    background-clip: text;
  }

  &__title {
    margin: 12px auto 0;

    text-align: center;
  }

  &__img {
    position: relative;

    max-width: 1200px;
    margin: 243px 0 0;

    @include vp-767 {
      margin: 101px 0 0;
    }
  }

  &__list {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;

    list-style: none;
  }

  &__item {
    position: absolute;
    transform: translateX(-50%);
    width: 9.67%;

    border-radius: 50%;
    border: 1px double transparent;
    background-image: $darkgrey-to-darkgrey, $white-to-transparent;
    background-origin: border-box;
    background-clip: padding-box, border-box;

    animation: pulse 2s ease-in-out alternate infinite;

    @include vp-767 {
      width: 10.55%;
    }
  }

  &__inner {
    position: relative;
    height: 0;
    padding-top: 100%;

    border: none;
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__icon--logo {
    width: 80%;
  }

  &__icon--lines {
    top: -15.5%;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 47.25%;

    @include vp-767 {
      top: -19.14%;
      width: 108%;
      height: 55.5%;
    }
  }

  &__item:nth-child(1) {
    top: 33.96%;
    left: 6.4%;

    animation-delay: 0.2s;

    @include vp-767 {
      left: 2.65%;
    }
  }

  &__item:nth-child(2) {
    top: 67px;
    top: 8.96%;
    left: 11.8%;

    animation-delay: 0.8s;

    @include vp-767 {
      left: 8.3%;
    }
  }

  &__item:nth-child(3) {
    top: -95px;
    top: -12.7%;
    left: 27.8%;

    animation-delay: 1.4s;

    @include vp-767 {
      left: 25.45%;
    }
  }

  &__item:nth-child(4) {
    top: -166px;
    top: -22.19%;
    left: 50.1%;

    animation-delay: 0.6s;
  }

  &__item:nth-child(5) {
    top: -95px;
    top: -12.7%;
    right: 17.9%;

    animation-delay: 0.5s;

    @include vp-767 {
      right: 14.9%;
    }
  }

  &__item:nth-child(6) {
    top: 67px;
    top: 8.96%;
    right: 1.85%;

    animation-delay: 1s;

    @include vp-767 {
      right: -2.25%;
    }
  }

  &__item:nth-child(7) {
    top: 254px;
    top: 33.96%;
    right: -3.6%;

    animation-delay: 1.2s;

    @include vp-767 {
      right: -7.9%;
    }
  }

  &__picture {
    @include vp-767 {
      margin: 0 -16px 0;
    }
  }

  &__decor {
    position: absolute;
    z-index: -1;
    left: 50%;
    transform: translateX(-50%);

    width: 100%;
  }

  &__bg {
    display: flex;
    justify-content: center;
  }

  &.anim-image {
    transition: transform $trans-600, opacity $trans-default;
    transform: translateY(30%);
    opacity: 0;

    will-change: transform, opacity;

    &.is-active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
