.subscription {
  position: relative;
  margin: -43px 0 0;
  padding: 0 0 140px;

  @include vp-1023 {
    margin: 21px 0 0;

    padding: 0 0 78px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 38px 74px 31px;

    border-radius: 4px;
    background: $blue-to-purple;

    @include vp-1023 {
      flex-direction: column;

      padding: 31px 17px;
    }
  }

  &__title {
    flex: 0 0 50%;
    margin: 0;

    @include vp-1023 {
      flex: 0 0 100%;
      text-align: center;
    }
  }

  &__form {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin: -5px 0 0;
    padding: 3px 4px 4px 14px;

    background-color: rgba($color-default-white, 0.07);
    border-radius: 2px;

    @include vp-1023 {
      width: 100%;
      margin: 8px 0 0;
    }
  }

  &__field {

    form {
      position: relative;
      display: flex;
      align-items: center;
    }

    @include vp-1023 {
      min-width: 220px;
    }
  }

  &__check {
    margin: 10px 7px 0 0;

    svg {
      width: 22px;
      height: 20px;
      color: $color-default-white;
    }
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    width: 50px;
    height: 50px;
    margin: 0 0 0 67px;

    background-color: rgba($color-default-white, 0.21);

    @include vp-1439 {
      margin: 0 0 0 auto;
    }

    @include vp-1023 {
      margin: 0 0 0 22.5%;
    }

    @include vp-419 {
      margin: 0 0 0 5%;
    }

    @include vp-374 {
      display: none;
    }
  }

  .custom-input {
    width: auto;
    padding: 0;

    border-radius: 5px;
    border: 0;

    input {
      width: 200px;
      min-height: 23px;
      max-height: 23px;
      padding: 5px 5px 5px 3px;
      border: 0;
      border-image-width: 0;
      border-radius: 5px;
      outline: none;

      font-weight: 400;
      font-size: 16px;
      line-height: 28px;

      color: $color-default-white;
      background-color: $color-transparent;
    }

    input:-moz-placeholder {
      color: $color-default-white;
    }

    input::-webkit-input-placeholder {
      color: $color-default-white;
    }
  }

  .custom-input label input:hover {
    border: none;
    outline: 1px solid $color-warning;
  }

  .custom-input label input:focus {
    border: none;
    outline: 1px solid $color-warning;
  }

  .input-message {
    position: absolute;

    &.is-invalid {
      left: 45px;
      bottom: -6px;

      font-size: 12px;
      color: $color-error;
    }

    &.is-valid {
      display: flex;
      align-items: center;

      left: 41px;
      bottom: 17px;
      width: 202px;
      min-height: 24px;
      max-height: 24px;
      padding: 0 6px 0 6px;

      color: $color-success;
      background-color: $color-default-white;
      border-radius: 5px;
      outline: 1px solid $color-success;

      font-size: 16px;

      // animation: disappearance 3s ease-in-out forwards;
    }
  }

  .custom-input.is-valid {
    outline: 1px solid $color-success;
    border: 0;
  }

  .custom-input.is-invalid {
    outline: 1px solid $color-error;
    border: 0;
  }

  &.anim-image {
    transition: transform $trans-600, opacity $trans-default;
    transform: translateY(30%);
    opacity: 0;

    will-change: transform, opacity;

    &.is-active {
      transform: translateY(0);
      opacity: 1;
    }
  }
}
