.block-video {
  position: relative;
  overflow: hidden;
  height: 100%;

  border-top-left-radius: 12px;
  border-top-right-radius: 12px;

  @include vp-1023 {
    border-radius: 12px;
  }
}

.block-video__wrapper {
  position: relative;
  // height: 27vw;
  height: 522px;

  @include vp-1499 {
    height: 36.35vw;
  }

  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  overflow: hidden;

  @include vp-1023 {
    height: 59vw;

    border-radius: 12px;
  }

  video {
    height: 100%;
    width: 100%;
    object-fit: cover;

    border-top-left-radius: 12px;
    border-top-right-radius: 12px;

    @include vp-1023 {
      border-radius: 12px;
    }
  }
}

.block-video__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  padding: 50px 33px;

  @include vp-1023 {
    padding: 19px 16px;
  }

  &.is-hidden {
    z-index: -1;
  }
}

.block-video__subtitle {
  padding: 3px 10px;

  background-color: rgba($color-default-white, 0.3);
  border-radius: 16px;
}

.block-video__text {
  margin: 11px 0 0;
}

.block-video__play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 100px;
  height: 100px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  line-height: 0;
  cursor: pointer;
  transform: translateX(-50%) translateY(-50%) translateZ(0);
  transition: all $trans-default;

  @include vp-1023 {
    width: 40px;
    height: auto;
  }

  &:hover,
  &:active {
    transform: translateX(-50%) translateY(-50%) translateZ(0) scale(1.2);
    outline: none;
  }

  svg {
    width: 100%;
    height: 100%;
    object-fit: contain;
    fill: none;
  }


  &.is-hidden {
    z-index: -1;
  }
}
