.accordion {
  margin: 20px 0 50px 0;

  & & {
    width: 90%;
    margin: 0 auto;
  }

  &__element:last-child {
    margin-bottom: 0;
  }

  &__element {
    margin-bottom: 34px;
    padding: 0;
  }

  &__element.is-active > &__button {
    svg {
      transform: rotate(0);
    }

    .choose__subtitle {
      // font-weight: 500;
      text-align: left;
    }
  }

  &__button {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 0;

    border: none;
    border-bottom: 1px solid rgba($color-default-white, 0.35);
    background-color: $color-transparent;
    cursor: pointer;
    opacity: 1;

    transition: opacity $trans-default;

    @include hover {
      opacity: 0.8;
    }

    @include hover-focus {
      opacity: 0.8;
    }

    svg {
      margin: 0 3px 0 0;
      flex-shrink: 0;
      transform: rotate(180deg);

      @include vp-1023 {
        margin: 0 12px 0 0;
      }
    }
  }

  &__wrapper {
    padding: 0;
  }
}
