.edition {
  position: relative;
  // overflow: hidden;

  width: 66.5%;
  margin: 40px 0 0;
  padding: 0 0 182px;

  @include vp-1023 {
    width: 100%;
    margin: -10px 0 0;
    padding: 0 0 90px;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;

    padding: 0;
  }

  &__breadcrumps {
    display: flex;
    // justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    // margin: 30px 0 0;

    // @include vp-1023 {
    //   margin: 24px 0 0;
    // }
  }

  &__link  {
    position: relative;
    z-index: 1;
    margin: 0;

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      top: 50%;

      height: 200%;
      width: 110%;
    }
  }

  &__icon  {
    margin: -1px 9px 0 8px;

    svg {
      width: 10px;
      height: 6px;
      transform: rotate(90deg);
    }
  }

  &__blog {
    @include vp-1023 {
      overflow: hidden;
      max-width: 67%;

      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  &__content {
    margin: 54px 0 0;

    @include vp-1023 {
      margin: 43px 0 0;
    }

    .edition__date {
      width: max-content;
      max-width: 100%;
      margin: 12px 0 0;

      color: $color-transparent;

      background-image: $frame-blue-to-purple;
      -webkit-background-clip: text;
      background-clip: text;

      @include vp-1023 {
        margin: 17px 0 0;
      }
    }
  }

  &__caption {
    display: flex;
    padding: 2px 10px;
    justify-content: center;
    align-items: center;

    max-width: max-content;

    border-radius: 16px;
    background: rgba($color-default-white, 0.30);
  }

  .edition__title {
    margin: 12px 0 0;

    @include vp-1023 {
      font-size: 32px;
      font-weight: 600;
      line-height: normal;
      letter-spacing: -0.16px;
    }
  }

  &__img {
    width: 99%;
    margin: 24px 0 5px;

    @include vp-1023 {
      width: 100%;
      margin: 24px 0 0;
    }

    img {
      width: 100%;
      object-fit: cover;
    }
  }

  p {
    width: 99%;
    margin: 24px 0 0;

    @include vp-1023 {
      width: 100%;
    }
  }

  &__block {
    display: flex;
    justify-content: space-between;

    width: 99%;
    margin: 80px 0 56px;

    img {
      width: 100%;
      object-fit: cover;
    }

    @include vp-1023 {
      width: 104%;
      margin: 32px 0 8px;

      img {
        width: 92%;
        // aspect-ratio: 16 / 10.7;
        object-fit: cover;
      }
    }
  }

  &__pict {
    flex: 0 0 47.77%;

    @include vp-1023 {
      flex: 0 0 49.5%;
    }
  }

  &__decor {
    position: absolute;
    z-index: -1;
    left: 50%;
    top: 3.7%;
    transform: translate(-50%,-50%);
    width: 430px;
    height: 220px;
    display: none;

    @include vp-1023 {
      display: block;
    }
  }

  ul {
    display: flex;
    flex-wrap: wrap;

    margin: 11px 0 0 -4px;
    padding: 0;
    list-style: none;

    color: $color-transparent;

    background-image: $frame-blue-to-purple;
    -webkit-background-clip: text;
    background-clip: text;
  }

  li {
    margin: 0 0 0 4px;
  }

  p.edition__share {
    margin: 18px 0 0;

    @include vp-1023 {
      margin: 24px 0 0;
    }
  }

  .socials__list {
    display: flex;
    flex-wrap: wrap;
    list-style: none;

    margin: -2px 0 0 -19px;
    padding: 0;
  }

  .socials__item {
    display: inline-flex;
    justify-content: center;
    align-items: center;

    width: 20px;
    height: 20px;
    margin: 19px 0 0 19px;
  }
}
