.large-small-picture {
  overflow: hidden;
  position: absolute;
  max-width: 1200px;

  &__inner  {
    position: relative;

  //   transform-style: preserve-3d;
  //   perspective: 4000px;

  //   transition: transform $trans-600;
  //   will-change: transform;
  }

  &__img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    display: block;
    width: 100%;
    max-width: 100%;
    object-fit: cover;
    height: 100%;

    // transition: transform $trans-600;
    // will-change: transform;

    img {
      width: 100%;
      object-fit: cover;
    }
  }

  // &__img--large {
  //   transform: translateZ(600px);
  // }

  // &__img--small {
  //   transform: translateZ(300px);
  // }
}


