.recent-article {
  position: sticky;
  top: 245px;

  align-self: flex-start;

  width: 27.75%;
  margin: 122px 0 0 5.75%;
  padding: 0 0 185px;

  @include vp-1023 {
    position: static;

    width: 100%;
    margin: 0;
    padding: 0 0 82px;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;

    padding: 0;
  }

  &__title {
    margin: 0;

    @include vp-1023 {
      margin: 8px 0 0;
    }
  }

  &__list {
    list-style: none;
    display: flex;
    flex-direction: column;

    margin: -1px 0 0 0;
    padding: 0;
  }

  &__item {
    position: relative;

    margin: 0;
    padding: 23px 0;

    transition: opacity $trans-default;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 1px;
      width: 100%;
      background-color: $color-default-white;
      opacity: 0.15;
    }

    @include hover {
      .recent-article__link {
        opacity: 0.5;
      }

      &::before {
        opacity: 1;
      }
    }
  }

  &__link {
    display: flex;
    align-items: flex-start;

    opacity: 1;
    transition: opacity $trans-default;

    img {
      width: 102px;
      height: 65px;
    }
  }

  &__block {
    width: 66%;
    margin: 0 0 0 17px;

    @include vp-1023 {
      width: 72%;
    }
  }

  &__card-title {
    display: flex;
    padding: 2px 7px 2px;
    justify-content: center;
    align-items: center;

    max-width: max-content;
    margin: 0;

    border-radius: 12px;
    background: rgba($color-default-white, 0.30);
  }

  &__text {
    display: -webkit-box;
    -webkit-line-clamp: 2; // количество строк
    overflow: hidden;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;

    width: 98%;
    margin: 5px 0 0;
    color: $color-default-white;
  }

  &__hashtag {
    margin: 50px 0 0;
  }

  &__hashtags {
    margin: 12px 0 0 -12px;
    display: flex;
    flex-wrap: wrap;

    span {
      display: flex;
      justify-content: center;
      align-items: center;

      width: max-content;
      max-width: 100%;
      margin: 11px 0 0 12px;
      padding: 2px 8px 2px 7px;

      border-radius: 12px;
      background: rgba($color-default-white, 0.30);

      @include vp-1023 {
        margin: 12px 0 0 12px;
        padding: 2px 8px 1px 7px;
      }
    }
  }
}
