.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 18px 38px;

  color: $color-default-white;
  background: $blue-to-purple;

  font-weight: 700;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  letter-spacing: 1.5px;

  border: none;
  cursor: pointer;
  user-select: none;

  transition: opacity $trans-default;

  &:disabled {
    opacity: 0.3;

    pointer-events: none;
  }

  @include hover {
    opacity: 0.8;
  }

  @include hover-focus {
    opacity: 0.8;
  }

  &--frame {
    padding: 10px 38px;

    color: $color-default-white;

    font-weight: 300;
    font-size: 16px;
    line-height: 28px;
    text-transform: none;
    letter-spacing: normal;

    // border: 1px double transparent;
    // background-image: $black-to-black, $frame-blue-to-purple;
    // background-origin: border-box;
    // background-clip: padding-box, border-box;
    background: none;

    border: 1px solid transparent;
    border-image: $blue-to-purple;
    border-image-slice: 1;
  }
}
